




































import {Component, Prop, Vue, Watch} from 'vue-property-decorator'
import $ from 'jquery'


@Component({})
export default class Modal extends Vue {
    @Prop({
        required: true,
        default: () => false
    })
    show!: boolean

    @Watch('show')
    watchShow(val: boolean): void {
        const $body = $('body')

        if (val) {
            $body.css('overflow', 'hidden')
        } else {
            $body.css('overflow', 'unset')
        }
    }
}
